class Worker {
    constructor(name, surname, position) {
        this._name = name;
        this._surname = surname;
        this._position = position;
    }

    get name() {
        return this._name;
    }

    get surname() {
        return this._surname;
    }

    get position() {
        return this._position;
    }

    set name(name) {
        this._name = name;
    }

    set surname(surname) {
        this._surname = surname;
    }

    set position(position) {
        this._position = position;
    }

    showPosition() {
        console.log(`Position = ${this._position}`);
    }
}

module.exports = Worker;